// ? Миксины

// * Основной шрифт

@mixin font-montserrat {
  font-family: "Montserrat", sans-serif;
}

// ! Основной шрифт

// * Градиенты

@mixin gradient-background {
  background-image: linear-gradient(90deg, #211b19 0%, #4e3427 100%);
}

// ! Градиенты

// ? /Миксины

// ? Стандартные стили

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  @include font-montserrat;
}

// ? /Стандартные стили

// ? Переменные

// * Цвета

$c-white: #ffffff; // h1, пункты меню, h2, h3, text, текст кнопок
$c-dust: #c7b299; // focused элементы
$c-arrowtown: #998675; // bg кнопок, focused фильтр
$c-rwhite: #fbfaf8; // bg 3th section
$c-cararra: #f1eee9; // разделители 2th section
$c-bcarrara: #ebebeb;
$c-stack: #8c8c8c; // text
$c-gray: #555555; // h2
$c-lgray: #d1d1d1; //section projects - подписи к картинкам
$c-wenge: #615855; // 4th section WP Theme h3
$c-blue: #00bff3; // имена пользователей в pre-footer
$c-mountain: #959595; // ссылка в pre-footer
$c-rblack: #252525; // разделители "|" в footer
$c-eblack: #191919; // bg pre-footer
$c-bunker: #111111; // bg footer
$c-thunder: #362f2d;
$c-desert: #f8f8f8;
$c-tuatara: #363636;
$c-sandal: #a48d72;

// ! Цвета

// * Размеры шрифтов
$fs-xxxs: 12px; // date pre-footer
$fs-xxs: 13px; // footer
$fs-xs: 14px; // пункты меню, text, read more
$fs-sm: 16px; // subtitle
$fs-md: 18px; // subtitle
$fs-lg: 20px; // h3
$fs-llg: 22px; // h2
$fs-xl: 28px; // h2
$fs-xxl: 30px; // h2
$fs-xxxl: 34px; // h1
$fs-superxl: 36px; // h3
$fs-superxxl: 48px; // counter
$fs-superxxxl: 52px; // h2
// 12/300 // date pre-footer +
// 13/300 // footer +
// 14/300 // пункты меню, text +
// 14/400 // read more
// 14/800 // button text
// 16/300 // subtitle
// 18/300 // subtitle
// 20/500 // h3
// 22/600 // h2
// 28/400 // h2
// 30/500 // h2
// 30/600 // h2
// 34/400 // h1
