// ? Компоненты

// ** Контейнеры

.container-inner {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;

  &_size_sm {
    max-width: 1100px;
  }

  &_size_wide {
    max-width: 1230px; // ??
  }

  &_size_xwide {
    max-width: 1620px;
  }
}

.container-outer {
  padding: 0 20px;
}

.flex-container {
  display: flex;
}

// !! Контейнеры

// ** Текст

.title {
  font-family: "Raleway", sans-serif;
  line-height: 1.2;

  &_style_section {
    font-size: 30px;
    font-weight: 600;
    color: $c-gray;
  }
}

.subtitle {
  font-size: 14px;
  font-weight: 300;
  color: $c-stack;

  &_size_xl {
    font-size: 16px;
    line-height: 28px;
  }

  &_size_xxl {
    font-size: 18px;
    line-height: 1.2;
  }
}

.suptitle {
  font-size: 36px;
  font-weight: 500;
  color: $c-white;
}

.main-title {
  font-size: 52px;
  font-weight: 800;
  color: $c-white;
}

.text {
  font-size: 16px;
  font-weight: 300;
  color: $c-white;
  text-align: center;
}

.separator {
  width: 70px;
  height: 3px;
  background-color: $c-cararra;
  border: none;

  &_focused {
    background-color: $c-dust;
  }
}

// !! Текст

// ** Кнопки

.button {
  text-transform: uppercase;
  padding: 21px 38px;
  background-color: $c-arrowtown;
  font-family: "Raleway", sans-serif;
  font-size: 14px;
  font-weight: 800;
  text-decoration: none;
  color: $c-white;
  border-radius: 3px;

  &_size_wptheme {
    padding: 20px 30px;
  }

  &_size_project {
    padding: 20px 45px;
  }
}

// !! Кнопки

