// ! WPtheme

// * Project

.project {
  &__title {
    margin-top: 81px;
    text-align: center;
  }

  &__subtitle {
    padding-top: 20px;
    text-align: center;
    width: 65%;
    margin: 0 auto;
  }

  &__filter {
    margin-top: 40px;
  }

  //&__projects {}
  &__button {
    text-align: center;
    margin-top: 59px;
    padding-bottom: 100px;
  }
}

.filter {
  text-align: center;

  &__item {
    padding: 10px 23px;
    border: 1px solid $c-bcarrara;
    border-radius: 3px;
    background-color: $c-white;
    font-size: 14px;
    line-height: 21px;
    color: $c-stack;

    &_focused {
      background-color: $c-arrowtown;
      color: $c-white;
    }
  }
}

.projects {
  margin-top: 50px;

  &__link-icon {
    // position: absolute;
    // top: 0;
    // bottom: 0;
    // left: 60%;
    // right: 40%;
    // margin: auto;
    // z-index: 999999;
    // display: none;
    // transition: 0.3s ease-in-out;
    display: none;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4) 100% 100%;
    transition: 0.3s ease-in-out;

    &-image {
      position: absolute;
      margin: auto;
      top: 0;
      bottom: 0;
      left: 39%;
      //right: 55%;
      transition: 0.3s ease-in-out;
    }
  }

  &__loupa-icon {
    // position: absolute;
    // top: 0;
    // bottom: 0;
    // left: 40%;
    // right: 60%;
    // margin: auto;
    // display: none;
    // transition: 0.3s ease-in-out;
    display: none;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.4) 100% 100%;
    transition: 0.3s ease-in-out;

    &-image {
      position: absolute;
      margin: auto;
      top: 0;
      bottom: 0;
      //left: 45%;
      right: 39%;
      transition: 0.3s ease-in-out;
    }
  }

  &__item {
    &-top {
      position: relative;
      transition: 0.3s ease-in-out;
    }

    &:nth-child(n + 4) {
      margin-top: 30px;
    }

    &-text {
      position: relative;
      padding: 30px 113px 27px 28px;
      background-color: $c-rwhite;
      transition: 0.3s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        /* Абсолютное позиционирование */
        left: 20px;
        top: -24px;
        /* Положение треугольника */
        border: 12px solid transparent;
        /* Прозрачные границы */
        border-bottom: 12px solid $c-rwhite;
        /* Добавляем треугольник */
        transition: 0.3s ease-in-out;
      }
    }

    &:hover &-top {
      transition: 0.3s ease-in-out;
    }

    &:hover .projects__title {
      color: $c-white;
    }

    &:hover .projects__subtitle {
      color: $c-dust;
    }

    &:hover &-text {
      background-color: $c-thunder;
      transition: 0.3s ease-in-out;
    }

    &:hover .projects__loupa-icon {
      display: block;
      transition: 0.3s ease-in-out;
    }

    &:hover .projects__link-icon {
      display: block;
      transition: 0.3s ease-in-out;
    }

    &:hover &-text::before {
      content: "";
      position: absolute;
      /* Абсолютное позиционирование */
      left: 20px;
      top: -24px;
      /* Положение треугольника */
      border: 12px solid transparent;
      /* Прозрачные границы */
      border-bottom: 12px solid $c-thunder;
      /* Добавляем треугольник */
      transition: 0.3s ease-in-out;
    }
  }

  &__image {
    margin-bottom: -4px;
    display: block;
    transition: 0.3s ease-in-out;
  }

  &__title {
    font-size: 20px;
    font-weight: 500;
    color: $c-dust;
    transition: 0.3s ease-in-out;
  }

  &__subtitle {
    margin-top: 5px;
    color: $c-lgray;
    transition: 0.3s ease-in-out;
  }

  &__flex-container {
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

