// ! Mobile

// * Counter

.counter {
  @include gradient-background;
  &__flex-container {
    padding-top: 69px;
    padding-bottom: 60px;
    justify-content: space-between;
  }
  &__item {
    display: flex;
    align-content: center;
    flex-direction: column;
    text-align: center;
  }
  &__image {
    align-self: center;
  }
  &__number {
    margin-top: 26px;
    font-size: 48px;
    line-height: 36px;
    font-weight: 200;
    color: $c-white;
  }
  &__subtitle {
    margin-top: 16px;
    padding-bottom: 28px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 300px;
    color: $c-dust;
    text-transform: uppercase;
  }
  &__separator {
    border: none;
    width: 30px;
    height: 2px;
    background-color: $c-white;
    margin: 0 auto;
  }
}

// ! Counter

// * Recent

.recent {
  &__title {
    text-align: center;
    margin-top: 82px;
  }
  &__subtitle {
    padding-top: 22px;
    text-align: center;
    width: 65%;
    margin: 0 auto;
  }
  &__flex-container {
    justify-content: space-between;
    padding-bottom: 40px;
  }
  &__item {
    max-width: 370px;
    margin-top: 34px;
    &-top {
      position: relative;
      margin-bottom: -5px;
    }
    &-body {
      padding: 30px 40px 33px 26px;
      border: 1px solid $c-desert;
    }
    &-title {
      font-size: 20px;
      font-weight: 500;
      color: $c-gray;
    }
    &-subtitle {
      margin-top: 16px;
    }
    &:hover .date__day {
      background-color: $c-dust;
      transition: 0.3s ease-in-out;
    }
    &:hover .date__month {
      background-color: $c-sandal;
      transition: 0.3s ease-in-out;
    }
    &:hover &-top {
      background-color: black;
      height: auto;
      transition: 0.3s ease-in-out;
    }
    &:hover .recent__image {
      opacity: 0.4;
      transition: 0.3s ease-in-out;
    }
    &:hover .date {
      opacity: 1;
      transition: 0.3s ease-in-out;
    }
    &:hover &-body {
      border: 1px solid $c-thunder;
      background-color: $c-thunder;
      transition: 0.3s ease-in-out;
    }
    &:hover &-title {
      color: $c-dust;
      transition: 0.3s ease-in-out;
    }
    &:hover &-subtitle {
      color: $c-white;
      transition: 0.3s ease-in-out;
    }
  }
  &__image {
    vertical-align: bottom;
  }
  &__more {
    margin-top: 24px;

    &-text {
      font-size: 14px;
      color: $c-dust;
      font-weight: 300;
      line-height: 20px;
    }
    &-icon {
      padding-left: 8px;
    }
  }
}

.date {
  z-index: 999999;
  position: absolute;
  left: 20px;
  top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $c-white;
  &__day {
    border-radius: 3px 3px 0px 0px;
    padding: 9px 11px;
    background-color: $c-tuatara;
    font-size: 24px;
    line-height: 20px;
    font-weight: 300;
  }
  &__month {
    border-radius: 0px 0px 3px 3px;
    padding: 7px 13px;
    background-color: $c-rblack;
    font-size: 12px;
    line-height: 20px;
    font-weight: 400;
  }
}
.slider-arrow {
